<template>
  <div>
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
        <a @click="$emit('showDrawer')" class="icon icon_manue"> manue </a>
      </div>
      <h1>flash-sales</h1>
      <div class="chartWrap">
        <router-link :to="{ path: '/search', query: {} }">
          <span class="search iconfont"> &#xe625; </span>
        </router-link>
        <span class="bag">
          <em class="font10"> {{ indexData.shoppingCartNum }} </em>
        </span>
        <a href="javascript:void(0)" @click="goCart" class="iconfont cart">
          &#xe669;
        </a>
      </div>
    </section>

    <section class="tabNav">
      <div @click="tabIdx = 1" :class="{ activeTab: tabIdx == 1 }">
        <p>24:00</p>
        <p>On going</p>
      </div>
      <div @click="tabIdx = 2" :class="{ activeTab: tabIdx == 2 }">
        <p>{{ latestTime[0] }}</p>
        <p>24:00</p>
      </div>
      <div @click="tabIdx = 3" :class="{ activeTab: tabIdx == 3 }">
        <p>{{ latestTime[1] }}</p>
        <p>24:00</p>
      </div>
    </section>

    <section class="topTimerWrap" v-if="tabIdx == 1">
      <div class="tittle">
        Hot sales
      </div>
      <div class="timerWrap">
        <span class="black">Ends in</span>
        <div class="timer">
          <template v-if="remainTime[0].d != '00'">
            <span> {{ remainTime[0].d }}</span> :
          </template>
          <span> {{ remainTime[0].h || "00" }} </span> :
          <span> {{ remainTime[0].m || "00" }} </span> :
          <span> {{ remainTime[0].s || "00" }} </span>
        </div>
      </div>
    </section>

    <section class="topTimerWrap" v-if="tabIdx == 2">
      <div class="tittle">
        Hot sales
      </div>
      <div class="timerWrap">
        <span class="black">Starts in</span>
        <div class="timer">
          <template v-if="remainTime[1].d != '00'">
            <span> {{ remainTime[1].d }}</span> :
          </template>
          <span> {{ remainTime[1].h || "00" }} </span> :
          <span> {{ remainTime[1].m || "00" }} </span> :
          <span> {{ remainTime[1].s || "00" }} </span>
        </div>
      </div>
    </section>

    <section class="topTimerWrap" v-if="tabIdx == 3">
      <div class="tittle">
        Hot sales
      </div>
      <div class="timerWrap">
        <span class="black">Starts in</span>
        <div class="timer">
          <template v-if="remainTime[2].d != '00'">
            <span> {{ remainTime[2].d }}</span> :
          </template>
          <span> {{ remainTime[2].h || "00" }} </span> :
          <span> {{ remainTime[2].m || "00" }} </span> :
          <span> {{ remainTime[2].s || "00" }} </span>
        </div>
      </div>
    </section>

    <section class="flashProList" v-if="tabIdx == 1">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="The end"
          @load="onLoad"
        >
          <ul class="clearfix">
            <li v-for="(item, index) in productList1" :key="index">
              <div class="tips_cutOff" v-if="item.discountRate">
                {{ item.discountRate }} OFF
              </div>
              <div class="imgWrap">
                <img class="img" :src="item.spuMainImage" />
              </div>
              <div class="rightMain">
                <div class="name">{{ item.name }}</div>
                <div class="priceWrap">
                  <p class="red">{{ item.priceRange }} AED</p>
                  <p class="gray">{{ item.priceRangeLine }} AED</p>
                </div>
                <div class="flashDownWrap">
                  <div class="bottomPrice">
                    <div class="process">
                      <p
                        class="activeBar"
                        :style="{ width: item.soldRate }"
                      ></p>
                      <span class="rightTips font10">
                        {{ item.soldRate }} sold
                      </span>
                    </div>
                  </div>
                  <div>
                    <van-button
                      type="danger"
                      @click="
                        $router.push({
                          path: 'proDetail',
                          query: { spuCode: item.spuCode }
                        })
                      "
                      >Shop Now</van-button
                    >
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </van-list>
      </van-pull-refresh>
    </section>

    <section class="flashProList" v-if="tabIdx == 2">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="The end"
          @load="onLoad"
        >
          <ul class="clearfix">
            <li v-for="(item, index) in productList2" :key="index">
              <div class="tips_cutOff" v-if="item.discountRate">
                {{ item.discountRate }} OFF
              </div>
              <div class="imgWrap">
                <img class="img" :src="item.spuMainImage" />
              </div>
              <div class="rightMain">
                <div class="name">{{ item.name }}</div>
                <div class="flashDownWrap">
                  <div class="bottomPrice">
                    <p class="red">{{ item.priceRange }} AED</p>
                    <p class="gray">{{ item.priceRangeLine }} AED</p>
                    <div class="process">
                      <p
                        class="activeBar"
                        :style="{ width: item.soldRate * 100 }"
                      ></p>
                      <span class="rightTips font10">
                        Only {{ item.num }}
                      </span>
                    </div>
                  </div>
                  <div>
                    <van-button
                      type="danger"
                      @click="
                        $router.push({
                          path: 'proDetail',
                          query: { spuCode: item.spuCode }
                        })
                      "
                      >See It</van-button
                    >
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </van-list>
      </van-pull-refresh>
    </section>

    <section class="flashProList" v-if="tabIdx == 3">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="The end"
          @load="onLoad"
        >
          <ul class="clearfix">
            <li v-for="(item, index) in productList3" :key="index">
              <div class="tips_cutOff" v-if="item.discountRate">
                {{ item.discountRate }} OFF
              </div>
              <div class="imgWrap">
                <img class="img" :src="item.spuMainImage" />
              </div>
              <div class="rightMain">
                <div class="name">{{ item.name }}</div>
                <div class="flashDownWrap">
                  <div class="bottomPrice">
                    <p class="red">{{ item.priceRange }} AED</p>
                    <p class="gray">{{ item.priceRangeLine }} AED</p>
                    <div class="process">
                      <p
                        class="activeBar"
                        :style="{ width: item.soldRate * 100 }"
                      ></p>
                      <span class="rightTips font10">
                        Only {{ item.num }}
                      </span>
                    </div>
                  </div>
                  <div>
                    <van-button
                      type="danger"
                      @click="
                        $router.push({
                          path: 'proDetail',
                          query: { spuCode: item.spuCode }
                        })
                      "
                      >See It</van-button
                    >
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </van-list>
      </van-pull-refresh>
    </section>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { getFlashSaleDay } from "@/api/flashSale";
import { clearTimeout } from "timers";
export default {
  name: "flashSale",
  data() {
    return {
      pageNo: 1,
      pageSize: 10,
      loading: false,
      finished: false,
      isLoading: true,
      productList1: [],
      productList2: [],
      productList3: [],
      latestTime: [],
      remainTime: [{}, {}, {}],
      timer: {
        d: "00",
        h: "00",
        m: "00",
        s: "00"
      },
      tabIdx: 1
    };
  },
  computed: {
    ...mapGetters(["indexData"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.pageNo = 1;
      let now = new Date();
      now.setDate(now.getDate() + 1);
      this.latestTime.push(now.getMonth() + 1 + "/" + now.getDate());
      now.setDate(now.getDate() + 1);
      this.latestTime.push(now.getMonth() + 1 + "/" + now.getDate());
      getFlashSaleDay(this.pageNo, this.pageSize)
        .then(res => {
          res = res.data.result.result;
          this.productList1 = res[0].productList;
          this.productList2 = res[1].productList;
          this.productList3 = res[2].productList;
          this.remainTime[0].remain = res[0].endTimeStr * 1;
          this.remainTime[1].remain = res[1].beginTimeStr * 1;
          this.remainTime[2].remain = res[2].beginTimeStr * 1;
          if (this.timeoutId) {
            clearTimeout(this.timeoutId);
          }
          this.countTime();
          this.isLoading = false;
          if (res.data.result.totalNum <= this.result.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.isLoading = false;
        });
    },
    countTime() {
      this.remainTime.forEach((element, index) => {
        let leftTime = element.remain;
        if (leftTime >= 0) {
          element.d = this.timeStr(Math.floor(leftTime / 60 / 60 / 24));
          element.h = this.timeStr(Math.floor((leftTime / 60 / 60) % 24));
          element.m = this.timeStr(Math.floor((leftTime / 60) % 60));
          element.s = this.timeStr(Math.floor(leftTime % 60));
          element.remain = element.remain - 1;
          this.$set(this.remainTime, index, element);
        }
      });

      //递归每秒调用countTime方法，显示动态时间效果
      this.timeoutId = setTimeout(this.countTime, 1000);
    },
    onLoad() {
      if (this.isLoading) {
        this.loading = false;
        return false;
      }
      this.pageNo++;
      getFlashSaleDay(this.pageNo, this.pageSize)
        .then(res => {
          res = res.data.result.result;
          this.productList1 = this.productList1.concat(res[0].productList);
          this.productList2 = this.productList2.concat(res[1].productList);
          this.productList3 = this.productList3.concat(res[2].productList);
         
          this.isLoading = false;
          if (res.data.result.totalNum <= this.result.length) {
            this.finished = true;
          }
        })
        .catch(e => {
          this.finished = true;
          this.loading = false;
        });
    },
    onRefresh() {
      this.init();
    },
    timeStr(count) {
      if (count < 10) {
        return "0" + count;
      } else {
        return count;
      }
    },
    goBack() {
      this.$router.back(-1);
    },
    goCart() {
      this.$router.push({
        path: "/cart",
        query: {}
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/flashSale.scss";
</style>



